
















































































































import { Vue, Component } from "vue-property-decorator";
import DbService from "../services/dbService";
import { DateTime } from "luxon";
import { EventModel } from "../models/bindingModel";
import { Route } from "vue-router";
import { Booking } from "../types";

@Component
export default class LocationRoomView extends Vue {
  dbService = new DbService();

  params: any = [];

  locationId = "";
  buildingName = "";
  roomId = "";

  intervalsDefault = {
    first: 5,
    // Minutes between each time slot
    minutes: 60,
    // How many slots
    count: 24 - 6,
    // Height of the individual time slots
    height: 48,
  };

  today = DateTime.now().toFormat("yyyy-MM-dd");
  start = this.today;

  events: EventModel[] = [];
  mode = "stack";
  weekdays = [1, 2, 3, 4, 5];
  intervals = this.intervalsDefault;
  color = "secondary";

  // initial state of time menu
  startMenu = false;

  // Get all bookings for the room in the given time range.
  async getEventsFromDb() {
    const startTime = DateTime.fromFormat(this.start, "yyyy-MM-dd")
      .set({ weekday: 1 })
      .toFormat("yyyy-MM-dd");

    const endTime = DateTime.fromFormat(startTime, "yyyy-MM-dd")
      .plus({ days: 6 })
      .toFormat("yyyy-MM-dd");
    const result = await this.dbService.getRoomBookingsInTimeRange(
      this.locationId,
      this.roomId,
      startTime,
      endTime
    );

    return result;
  }

  // Add name and colour to events
  async getEvents() {
    const allEvents: EventModel[] = [];

    const events: Booking[] = await this.getEventsFromDb();

    for (const event of events) {
      allEvents.push({
        name: event.courseCode,
        start: event.start,
        end: event.end,
        color: "",
      });
    }

    return allEvents;
  }

  async mounted() {
    this.locationId = this.$route.params["locationId"];
    this.buildingName = await this.dbService.getBuildingByLocation(
      this.locationId
    );

    this.today = DateTime.now().toFormat("yyyy-MM-dd");
    this.roomId = this.$route.params["roomId"];
    this.start = this.$route.params["datetime"] ?? this.today;

    if (this.roomId == null) this.roomId = "";

    this.events = await this.getEvents();
  }

  async updateDateTime() {
    this.events = await this.getEvents();
  }
}
